			"script": "singlePlay(\"The ferret leaps out of your pants and attacks the mass of ducks. It's a blood bath. Feathers fly everywhere. You estimate the ferret takes down 147 ducks before succumbing to the duck billed fury. You will miss him dearly. Your pants will never feel full again\", \"\")\nremoveItemFromInventory('ferret')\naddItemToScene(\"duck bladders\", \"The Parade of Ducks\")\nremoveItemFromScene(\"ducks\", \"The Parade of Ducks\")",
