			"script": "if(!isVisible(\"Steve\")){\n    addItemToScene('Steve', 'bathroom')\n    play(`You add the laxative to the tea while Steve is away. The pills dissolves quickly until it is completely unnoticable. You think you might have added too much...\n    \n    Moments later, Steve returns. He sits and takes a long drink from his hot cup of tea. He sits back in pure anguish. Then, his eyes buldge, there is an unexpected level of anguish in his sip! He sits up right and then burts into a sprint for the bathroom. He might be there for a while.`, \"\")\n    changeInSceneDescription('Steve', {text: \"Steve occupies one of the toilets. A foul and ungodly smell eminates from the stall\", audio: \"\"})\n    changeDescription(\"Steve\", [{text: \"He's in the stall. He doesn't sound like he's having fun.\" ,audio:\"\"}])    \n}else{\n    play(\"I think he'll notice.\")\n}\n",
